
// export default ContestsList
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
// ** Invoice List Sidebar

// import AddQuiz from '../add'
import Flatpickr from 'react-flatpickr'
// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'
import { useForm, Controller } from 'react-hook-form'
import classnames from 'classnames'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter, HelpCircle } from 'react-feather'
import DataTable from 'react-data-table-component'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import {
  Card, 
  //Input,
  Row,
  Col,
  Label,
  FormGroup,
  //Form,
  //CustomInput,
  Spinner,
  Button,
  Popover,
  PopoverHeader,
  PopoverBody
} from 'reactstrap'
import AppCollapse from '@components/app-collapse'
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import {
  // getContestsListCSV,
  getcricketwinnerdata,
  getcricketwinnerdownload,
  winnerfrom,
  winnerto,
  contestfrom,
  contestto,
  quizfrom,
  quizto,
  clearData,
  getdaterange
  //getfootballcontestdownload 
} from '../../store/actions'
  
const ContestsList = () => {
  // ** Store Vars
  const CustomHeader = ({
    //handleFilter,
    //searchTerm,
    currentPage,
    setCurrentPage
  }) => {
    const defaultValues = {
      reactSelect: null
    }
    const { handleSubmit, control } = useForm({ defaultValues })
    const [popoverOpen, setPopoverOpen] = useState(false)
    const [val, setVal] = useState(false)
    const store = useSelector((state) => state.rep)
    const [picker, setPicker] = useState(store.winner_from)
    const [dateto, setDateto] = useState(store.winner_to)
  const dispatch = useDispatch()
  const edittime = (date) => {
    setVal(false)
    const local = new Date(date)
    const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setPicker(datestring)
  }
 
  const edit = (date) => {
    setVal(false)
    const local = new Date(date)
    const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setDateto(datestring)
  }

  const dateConverter = (startDate, timeEnd) => {
    const newStartDate = new Date(startDate)
    const newEndDate = new Date(timeEnd)
    const one_day = 1000 * 60 * 60 * 24
    let result = ''
    result = Math.ceil((newEndDate.getTime() - newStartDate.getTime()) / (one_day))
    console.log('date Converter result', result)
    if (result < 0) { return 0 }
    dispatch(getdaterange(result))
    return result
  }

  const onSubmit = (e) => {
    e.preventDefault()
    setVal(true)
    if ((picker === "" || picker === 'NaN-NaN-NaN')  || (dateto === "" || dateto === 'NaN-NaN-NaN')) {
      // console.log(picker)
    } else {
      setCurrentPage(1)
      dispatch(getcricketwinnerdata(currentPage, picker, dateto))
      dispatch(winnerfrom(picker))
      dispatch(winnerto(dateto)) 
      dateConverter(picker, dateto)
    }
  }
    //curentpage
    const download = () => {
      dispatch(getcricketwinnerdownload(picker, dateto))
    }
    console.log(store.date_range)
    const data = [
      {
        title: <div><Filter size={18}/> Filter...</div>,
        content: (
          <>
          <Row className='mx-auto'>
            <Col  className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            
            <Label className="mb-0">From:</Label>&nbsp;&nbsp;
            {/* <Flatpickr
                  value={store.winner_from}
                  id='range-picker'
                  className='form-control'
                  //  value={picker}
                  //  onChange={date => setPicker(date)}
                  onChange={date => edittime(date)}
      
                 /> */}
                   <Controller
              // as={Select}
              control={control}
              render={({ field: { ...field } }) => (
                <Flatpickr
                  {...field}
                  value={store.winner_from}
                  placeholder='select from date'
                  onChange={date => edittime(date)}
                  options = {{
                    // defaultDate : '01-05-1999',
                    dateFormat: "Y-m-d"
                }}
                id='range-picker'
                  // theme={selectThemeColors}
                  className={classnames('form-control', { 'is-invalid':  (picker === "" && val) || (picker === 'NaN-NaN-NaN' && val) })}
                  // classNamePrefix='select'
                  // isClearable={true}
                  />
                  )}
                  name='reactSelect'
              // defaultValue={series[0]}
            />
         &emsp;&nbsp;
            
            <Label className="mb-0">To:</Label>&nbsp;&nbsp;   
            {/* <Flatpickr
                    value={store.winner_to}
                    id='range-picker'
                    className='form-control'
                    onChange={dat => edit(dat)}
                 />  */}
                                   <Controller
              // as={Select}
              control={control}
              render={({ field: { ...field } }) => (
                <Flatpickr
                  {...field}
                  value={store.winner_to}
                  placeholder='select to date'
                  onChange={dat => edit(dat)}
                  options = {{
                    // defaultDate : '01-05-1999',
                    dateFormat: "Y-m-d"
                }}
                id='range-picker'
                  // theme={selectThemeColors}
                  className={classnames('form-control', { 'is-invalid':  (dateto === "" && val) || (dateto === 'NaN-NaN-NaN' && val) })}
                  // classNamePrefix='select'
                  // isClearable={true}
                  />
                  )}
                  name='reactSelect'
              // defaultValue={series[0]}
            />
             &emsp;&nbsp;
          </div>
        </Col>
          </Row>
          {(picker === "" && val) || (picker === 'NaN-NaN-NaN' && val) || (dateto === "" && val) || (dateto === 'NaN-NaN-NaN' && val) ?  <h5 className='text-danger ml-3'>Please select a date</h5> : ""}
          <Row className='ml-1 mt-1'>
        <FormGroup className="d-flex mb-0">
              <Button.Ripple
                className="mx-2"
                color="primary"
                type="submit"
                onClick={(e) => handleSubmit(onSubmit(e))}
              >
                Submit
              </Button.Ripple>
        </FormGroup> 
      </Row>
        </>
        )
      }
    ]

    return (
      <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
        <AppCollapse data={data} type='border' accordion className='mb-1' />
        <Row>
          <Col
            xl="6"
            className="ml-auto d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1">
           
            </div>
            <Button.Ripple 
                color='primary'
                disabled={store.date_range > 90}
                onClick ={() => download()}>      
                {store.download_loading ?  
                <>
                <Spinner color='white' size='sm' />
                <span className='ml-50'>Loading...</span>
                </> : 'Download CSV' }
      </Button.Ripple>
      <Button.Ripple
                outline id='controlledPopover'
                color='flat-info'
                >
                  <HelpCircle />
                </Button.Ripple>
      <Popover
        placement='top'
        target='controlledPopover'
        isOpen={popoverOpen}
        toggle={() => setPopoverOpen(!popoverOpen)}
      >
        <PopoverHeader>INFO</PopoverHeader>
        <PopoverBody>
                  pick a date less than 90 days to download
        </PopoverBody>
      </Popover>
          </Col>
        </Row>
        <div>       

        </div>
      </div>
    )
  }

  const store = useSelector((state) => state.rep)
  const {cricket_winner} = store
  console.log(cricket_winner)
  // ** States
  const [searchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
   const dispatch = useDispatch()
useEffect(() => {
    if (store.isloading) {
      <Spinner/>
    } else {
      setListData(store.cricket_winner)
    }
  }, [store.isloading])

  useEffect(() => {
    dispatch(getcricketwinnerdata(currentPage, store.winner_from, store.winner_to))
    dispatch(contestfrom(""))
    dispatch(contestto(""))
    dispatch(quizfrom(""))
    dispatch(quizto(""))
  }, [currentPage])

  const handlePagination = (page) => {
  
    setCurrentPage(page.selected + 1)
  }
 useEffect(() => {
    if (searchTerm) {
      const filterList = store.cricket_winner.filter((userItem) => {

        if (userItem.contest_name) {
          return userItem.contest_name.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.cricket_winner)
    }
  }, [searchTerm])

  useEffect(() => {
    console.log('Mounted')
    return () => {
      console.log('Unmounted')
      dispatch(clearData())
    }
  }, [])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    // const count = Number(Math.ceil(listData.length / rowsPerPage))
    //const count = store.cricket_contest_perpage.length
    // const count = store.cricket_contest_perpage.filter(page => page.label !== "&laquo; Previous" && page.label !== 'Next &raquo;').length
    const count = store.cricket_winner_perpage.last_page
      //console.log(count)
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
 
    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric',  hour: 'numeric', minute: 'numeric', second: 'numeric'}) => {
    if (!value) return value 
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
   
  }
  
  const columns = [
    {
      name: '#',
      minWidth: '20px',
      selector: '#',
      sortable: true,
      cell: (row, pos) => `#${store.cricket_winner_perpage.from + pos}`
    },
    {
      name: 'Fixture Date',
      minWidth: '200px',
      selector: 'fixture.date_time',
      sortable: true,
      cell: (row) => formatDate(row?.fixture?.date_time)
    },
    {
      name: 'Fixture Name',
      minWidth: '250px',
      selector: 'fixture.fixture_name',
      sortable: true,
      cell: (row) => row?.fixture?.fixture_name
    },
    {
      name: 'Contest ID',
      minWidth: '20px',
      selector: 'contest_id',
      sortable: true,
      cell: (row) => row?.contest_id
    },
    {
      name: 'Contest Name',
      minWidth: '160px',
      selector: 'contests.caption',
      sortable: true,
      cell: (row) => row.contests?.caption
    },
    {
      name: 'Entry Fees',
      minWidth: '160px',
      selector: 'contests.entry_fee',
      sortable: true,
      cell: (row) => row.contests?.entry_fee
    },
    {
      name: 'Total Entries',
      minWidth: '160px',
      selector: 'u_id',
      sortable: true,
      cell: (row) => row?.u_id
    },
    {
      name: 'Winning Coins',
      minWidth: '160px',
      selector: 'id',
      // sortable: true,
      cell: () => "-"
    },
    {
      name: 'Winning Merchandise',
      minWidth: '160px',
      selector: 'id',
      // sortable: true,
      cell: () => "-"
    },
    
    {
      name: 'Rank',
      minWidth: '160px',
      selector: 'rank',
      sortable: true,
      cell: (row) => row?.rank
    },
  
    {
      name: 'Total Score',
      minWidth: '160px',
      selector: 'score',
      sortable: true,
      cell: (row) => row?.score
    },
    {
      name: 'User Teamname',
      minWidth: '160px',
      selector: 'user.team_name',
      sortable: true,
      cell: (row) => row?.user?.team_name
    },
    {
      name: 'Team',
      minWidth: '160px',
      selector: 'team_id',
      sortable: true,
      cell: (row) => row?.team_id
    },
    
    {
      name: 'Email ID',
      minWidth: '160px',
      selector: 'user.email',
      sortable: true,
      cell: (row) => row?.user?.email
    },
    {
      name: 'Phone Number',
      minWidth: '160px',
      selector: 'user.mobile',
      sortable: true,
      cell: (row) => row?.user?.mobile
    }
  ]  

  return (
    
   <Fragment>
      <Card>  
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              //handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              // handleFilter={handleFilter}
            />
          }
        />
      </Card>
    </Fragment>
   
  )
}

export default ContestsList
